body {
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
    padding: 10px;
  }

  th {
    text-align: center;
    border: 1px solid black;
    background: #018ded;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
  }
.checkout {
    display: flex;
    padding: 20px;
    background-color: white;
    height: max-content;
}

.checkout__ad{
    width: 100%;
    margin-bottom: 10px; 
  
}

.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}