.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.checkoutProduct__info{
    padding-left: 20px;
}

.checkoutProduct__info > button {
    background:  #cfc0c075;
    border: 1px solid;
    margin-top: 10px;
    border-color: #f0ede650 #d4c49b21 #c9b6882d;
    color: #111;
    cursor: pointer;
    outline:none !important;
}

.checkoutProduct__image {
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.checkoutProduct__rating {
    display: flex;
}

.checkoutProduct__title {
    font-size: 17px;
    font-weight: 800;
}
#more:hover {
    background-color: rgba(172, 137, 137, 0.404);
}