.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
    margin-left: 10px;
}

.subtotal__gift{
    display: flex;
    align-items: center;
    
  
}


.subtotal > button {
    background: #cfc0c075;
    border: 2px solid;
    margin-left: 6%;
    border-color: #f0ede650 #d4c49b21 #c9b6882d;
    cursor: pointer;
    outline:none !important;
    width: 180px;
    margin-top: 10%;

    height: auto;
    display: flex;
    border-radius: 4px;
    font-family: 'Roboto', sans-serif bold;
    position: absolute;
}

.subtotal__gift > input {
    margin-bottom: 1px;
    margin-right: 1px;

}
