.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 600px;
    min-width: 100px;
    z-index: 1;
    box-shadow: 2px 2px 8px rgb(255, 255, 255);
  
}
.product__rating{
    display: flex;
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
    
}
.product > button{
    background: #cfc0c075;
    border: 2px solid;
    margin-top: 10px;
    border-color: #f0ede650 #d4c49b21 #c9b6882d;
    cursor: pointer;
    outline:none !important;
    width: 140px;
    height: 40px;
    border-radius: 8px;
    font-family: 'Roboto', sans-serif bold;
}

.product__price {
margin-top: 5px;

}
.product__info{
    height: 100px;
    margin-bottom: 15px;
   
    
    
}
