.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
  
}
.home__row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
.home__image {
    width: 100%;
    height: auto;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    border-radius: 5px;
    
}
.img-1{
    box-shadow: 2px 2px 8px rgba(93, 128, 48, 0.37);
    border-radius: 130px 130px 130px 130px;
}
