body {
    background-color:  #ececec;
    position: relative;
	min-height: 100vh;
}


.login {
    background-color: #ececec;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;   
    position: fixed;
    top: 50%;
    left: 50%;
    /* bring your own prefixes */
    transform: translate(-50%, -50%);
      

  }
    



.login__logo {
    margin-top: 50px;
    margin-bottom: 30px;
    object-fit: contain;
    width: 100px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 2px 2px 8px rgba(91, 94, 88, 0.171);
    border-radius: 130px 130px 130px 130px;

    
   
}

.login__container {
    width: 300px;
    height: fit-content;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid rgb(163, 160, 160);
    padding: 20px;
    align-items: center;
    box-shadow: 2px 2px 8px rgba(70, 77, 62, 0.37);
    
    
}
.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container > form > h5 {
    margin-bottom: 5px;
}

.login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background: white;
    width: 98%;
    display: flex;
  outline: none;
    
    
}


.login__container > p {
    margin-top: 1px;
    font-size: 12px;
    display: flex;
    align-items: center;
    white-space: pre-wrap;
  
   
    
    
}

.login__SignInButton{
    background:  #dfd7d7;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    margin-bottom: 20px;
    border-color: #94909080 #645b5b70 #af9b9b77;
    cursor: pointer;
    

}

.login__registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: rgba(169, 169, 169, 0.473);
    background:  #dfd7d7;
    cursor: pointer;
}

.input {
    border-color: #2c282857 #302a2a50 #4741414d;

}

