.header {
    height: 55px;
    display: flex;
    align-items: center;
    background-color: #ececec;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header__logo {
    width: 55px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: -7px;
    box-shadow: 2px 2px 8px rgba(93, 128, 48, 0.37);
    border-radius: 130px 130px 130px 130px;
   

}

.header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 26px;
    position: relative;
}

.header__searchInput {
    height: 30px;
    padding: 10px;
    border: none;
    width: 100%;
    outline-style: none;
    
    
}

.header__searchIcon {
    padding: 5px;
    height: 30px !important;
    background-color: #ffffff;
}

.header__optionLineOne{
    font-size: 10px;
   
    


}

.header__optionLineTwo{
    font-size: 11px;
    font-weight: 800;
   
    ;
}
    .header__optionBasket {
        display: flex;
        align-items: center;
        color: grey;

    }
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}
    

  


.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: grey;
}